import React from "react";
import "./card.styles.css";

const imgDict = {
    "Stark" : "data:image/jpeg;base64,",
    "Baratheon" : "data:image/png;base64,",
    "Lannister" : "data:image/png;base64,",
    "Tarly" : "data:image/jpeg;base64,",
    "Targaryen" : "data:image/jpeg;base64,",
    "Targaryan" : "data:image/jpeg;base64,",
    "Lanister" : "data:image/png;base64,",
    "Greyjoy" : "data:image/png;base64,",
    "Clegane" : "data:image/png;base64,",
    "Baelish" : "data:image/png;base64,",
    "Seaworth" : "data:image/png;base64,",
    "Tyrell" : "data:image/jpeg;base64,",
    "Tarth" : "data:image/png;base64,",
    "Bolton" : "data:image/png;base64,",
    "Mormont" : "data:image/png;base64,",
    "Viper" : "data:image/png;base64,",
    "Sand" : "data:image/png;base64,",
}
export const Card = (props) => {
    const house = props.data.family.split(" ");
    var house_img = "";
    if(house.length > 1){
        house_img = house[1];
    }
    else{
        house_img = house[0];
    }
    return <div className="card-container">
        <div className="flip-card">
            <div className="flip-card-inner">
                <div className="flip-card-front">
                    <img className="img" alt="GoT_pic" src={props.data.imageUrl}/>
                    <h2>{props.data.fullName}</h2>
                    <p>{props.data.title}</p>
                </div>
                <div className="flip-card-back">
                    <img className="img-back" alt="" src={imgDict[house_img] == null ? "data:image/jpeg;base64," : imgDict[house_img]}/>
                    <p>{props.data.family}</p>
                </div>
            </div>
        </div>
    </div>
}